<template>
	<view>
		<navbar v-model="screenHeight" title-color="#ffffff" background="#1b43c4" :iSimmersive="false" :isPrev="false" :placeholder="true" title="消息中心"></navbar>
		<mescroll-body 
		ref="mescrollRef" 
		@init="mescrollInit" 
		@down="downCallback" 
		@up="upCallback" 
		:down="downOption" 
		:up="upOption"
		:height="screenHeight+'px'"
		>
			<view class="list-box">
				<view class="list-item" v-for="(item,index) in result" :key="index" @click="jump(item)">
					<view class="list-item-content">
						<view class="avatar"><image v-if="item.avatar" :src="item.avatar"></image></view>
						<view class="content">
							<view class="title">{{ item.nickname }}</view>
							<view class="intro">{{ item.message }}</view>
						</view>
					</view>
					<view class="list-item-time">
						<view class="time">{{ item.last_time }}</view>
						<view class="badge" v-if="item.count > 0"><uni-badge :text="item.count" type="error" size="small"></uni-badge></view>
					</view>
				</view>
				
			</view>
		</mescroll-body>
		
		<authorize v-model="isAuthShow"></authorize>
	</view>
</template>

<script>
	import MescrollMixin from "@/uni_modules/mescroll-uni/components/mescroll-uni/mescroll-mixins.js";
	import navbar from "@/components/navbar/navbar";
	import authorize from "@/components/authorize/authorize";
	
	export default {
		mixins: [MescrollMixin],
		components: { navbar,authorize },
		data() {
			return {
				isAuthShow: false,
				result: [],
				downOption: {
					auto: false
				},
				upOption: {
					use: true, // 是否启用上拉加载; 默认true
					auto: false, // 是否在初始化完毕之后自动执行上拉加载的回调; 默认true
					isLock: false, // 是否锁定上拉加载,默认false;
					isBoth: true, // 上拉加载时,如果滑动到列表顶部是否可以同时触发下拉刷新;默认true,两者可同时触发;
					page: {
						num: 0, // 当前页码,默认0,回调之前会加1,即callback(page)会从1开始
						size: 10, // 每页数据的数量
						time: null // 加载第一页数据服务器返回的时间; 防止用户翻页时,后台新增了数据从而导致下一页数据重复;
					},
					noMoreSize: 3, // 如果列表已无数据,可设置列表的总数量要大于等于5条才显示无更多数据;避免列表数据过少(比如只有一条数据),显示无更多数据会不好看
					offset: 80, // 距底部多远时,触发upCallback(仅mescroll-uni生效, 对于mescroll-body则需在pages.json设置"onReachBottomDistance")
					bgColor: "transparent", // 背景颜色 (建议在pages.json中再设置一下backgroundColorTop)
					textColor: "gray", // 文本颜色 (当bgColor配置了颜色,而textColor未配置时,则textColor会默认为白色)
					textLoading: '加载中 ...', // 加载中的提示文本
					textNoMore: '-- END --', // 没有更多数据的提示文本
					
					empty: {
						use: true, // 是否显示空布局
						icon: this.$http.domain()+"static/images/empty.png", // 图标路径
						tip: '~ 暂无相关数据 ~', // 提示
						btnText: '', // 按钮
						fixed: false, // 是否使用fixed定位,默认false; 配置fixed为true,以下的top和zIndex才生效 (transform会使fixed失效,最终会降级为absolute)
						top: "100rpx", // fixed定位的top值 (完整的单位值,如 "10%"; "100rpx")
						zIndex: 99 // fixed定位z-index值
					}
				},
				screenHeight:0
			};
		},
		onShow() {
			this.$store.dispatch("usersStatus").then(()=>{
				this.isAuthShow = false;
				this.downCallback();
			}).catch(()=>{
				this.isAuthShow = true;
				this.mescroll.showEmpty();
			});
		},
		methods: {
			downCallback(){
				setTimeout(()=>{
					this.mescroll.resetUpScroll();
				},200);
			},
			// 主动触发下拉刷新
			triggerDownScroll(){
				this.mescroll.triggerDownScroll();
			},
			upCallback(page) {
				this.$http.getServiceList({
					page: page.num
				}).then((result)=>{
					this.mescroll.endByPage(result.data.list.length, result.data.total);
					if(result.status==1){
						if(page.num == 1) this.result = [];
						this.result = this.result.concat(result.data.list);
					}else if(result.status == -1){
						this.mescroll.endErr();
					}
				}).catch(error=>{
					this.mescroll.endErr();
				});
			},
			jump(data){
				this.$utils.navigateTo("message/view",{ id: data.id });
			}
		}
	};
</script>

<style lang="scss">
	page { background-color: #fff; }
	.list-box {
		display: flex; flex-direction: row;flex-wrap: wrap; 
		.list-item { 
			width: 100%;
			height: 134rpx;
			position: relative;
			border-bottom:1px solid #eee;
			.list-item-content { 
				padding-right: 172rpx;
				height: 134rpx;
				position: relative;
				.avatar { 
					border: 1px solid #ccc;
					position: absolute;
					top: 15rpx; left: 30rpx; background-color: #ccc;
					width: 100rpx; height: 100rpx; border-radius: 50rpx;
					image { border-radius: 50rpx; width: 100rpx; height: 100rpx; }
				}
				.content { 
					padding-left: 150rpx; padding-right: 30rpx;
					height: 134rpx;
					.title {
						font-size: 28rpx; color: #000; padding-top: 25rpx;
						white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
					}
					.intro { 
						font-size: 24rpx; color: #888; padding-top: 10rpx;
						white-space:nowrap; overflow:hidden; text-overflow:ellipsis;
					}
				}
			}
			.list-item-time { 
				position: absolute;
				right: 0; top:0;
				height: 134rpx;
				width: 172rpx;
				.time{ 
					width: 160rpx;
					position: absolute;  text-align: right;
					top:30rpx; font-size: 28rpx;
					right: 30rpx;
				}
				.badge { position: absolute; top: 75rpx; right: 30rpx; }
			}
		}
	}
</style>
